<template>
  <div  class="flex flex-col items-center text-white py-12">
      <div v-if="isLoaded">
        <h1 class="text-4xl mb-2">{{ city.city }}</h1>
        <p class="text-sm mb-12">
          {{
            new Date(weatherData.currentTime).toLocaleDateString(
              "en-us",
              {
                weekday: "short",
                day: "2-digit",
                month: "long",
              }
            )
          }}
          {{
            new Date(weatherData.currentTime).toLocaleTimeString(
              "en-us",
              {
                timeStyle: "short",
              }
            )
          }}
        </p>
        <p class="text-8xl mb-8">
          {{ Math.round(weatherData.current.temp) }}&deg;
        </p>
        <p>
          Feels like
          {{ Math.round(weatherData.current.feels_like) }} &deg;
        </p>
        <p class="capitalize">
          {{ weatherData.current.weather[0].description }}
        </p>
        <img
          class="w-[150px] h-auto"
          :src="
            `http://openweathermap.org/img/wn/${weatherData.current.weather[0].icon}@2x.png`
          "
          alt=""
        />
        WeatherBlock timeZone {{ weatherData.timezone }} temp {{  }}
      </div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
  name: "WeatherBlock",
  props: {
    city: {
      type: Object,
      default: {},
    },
  },
  data() {
      return {
        weatherData: {},
        isLoaded: false
      };
    },
    methods: {
      //  getWeatherData: async function() {
      //  await axios
      //   .get(
      //     `https://api.openweathermap.org/data/2.5/onecall?lat=${this.city.geo.lat}&lon=${this.city.geo.lng}&exclude={part}&appid=7efa332cf48aeb9d2d391a51027f1a71`
      //   )
      //   .then((response) => {
      //     weatherData = response.data;
      //     const localOffset = new Date().getTimezoneOffset() * 60000;
      //     const utc = weatherData.current.dt * 1000 + localOffset;
      //     weatherData.currentTime = utc + 1000 * weatherData.timezone_offset;
      //     return weatherData
      //   })
      //   .catch((e) => console.log(e));
      //  }
      async getWeather() {
        try {
          const weather = await axios.get(`https://api.openweathermap.org/data/2.5/onecall?lat=${this.city.geo.lat}&lon=${this.city.geo.lng}&exclude={part}&appid=7efa332cf48aeb9d2d391a51027f1a71`);
          // cal current date & time
    const localOffset = new Date().getTimezoneOffset() * 60000;
    const utc = weatherData.data.current.dt * 1000 + localOffset;
    weatherData.data.currentTime =
      utc + 1000 * weatherData.data.timezone_offset;

    // cal hourly weather offset
    weatherData.data.hourly.forEach((hour) => {
      const utc = hour.dt * 1000 + localOffset;
      hour.currentTime =
        utc + 1000 * weatherData.data.timezone_offset;
    });
        this.weatherData = weather.data
        this.isLoaded = true;
        } catch (error) {
          alert('ERror')
        }
      }
    },
  mounted () {
   this.getWeather()
  },
};
</script>

<style scoped></style>
