<template>
  <div class="flex-center-column">
    <div class="flex-center-column">
      <input
        type="text"
        class="input-search"
        placeholder="Search Input"
        v-model="searchInputValue"
        @input="getSearchResult"
      />
      <p v-if="isLoading">Loading....</p>
      <p
        v-if="
          !serverError &&
          apiSearchResult.length === 0 &&
          searchInputValue !== '' &&
          !isLoading &&
          cities.length < 5
        "
      >
        No result.
      </p>
      <ul class="city-list" v-if="apiSearchResult">
        <li
          v-for="searchResult in apiSearchResult"
          :key="searchResult.id"
          @click="getCity(searchResult)"
        >
          {{ searchResult.place_name }}
        </li>
      </ul>
      <p v-if="tooMutchCities">Too mutch cities remove something please</p>
      <p v-if="isError">Opps....</p>
    </div>

    <div>
      <CityList :cities="cities"/>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import CityList from '../components/CityList.vue'

export default {
  name: "Home",
  components: {
    CityList
  },
  data() {
    return {
      searchInputValue: "",
      searchTimeout: null,
      apiSearchResult: [],
      isError: false,
      isLoading: false,
      ApiKey:
        "pk.eyJ1Ijoiam9obmtvbWFybmlja2kiLCJhIjoiY2t5NjFzODZvMHJkaDJ1bWx6OGVieGxreSJ9.IpojdT3U3NENknF6_WhR2Q",

      cities: [],
      tooMutchCities: false,
    };
  },
  methods: {
    getSearchResult() {
      if(this.cities.length < 5){
        this.tooMutchCities = false;
        (this.isLoading = true), clearTimeout(this.searchTimeout);
      this.searchTimeout = setTimeout(async () => {
        if (this.searchInputValue !== "") {
          try {
            const res = await axios.get(
              `https://api.mapbox.com/geocoding/v5/mapbox.places/${this.searchInputValue}.json?access_token=${this.ApiKey}&types=place`
            );
            this.apiSearchResult = res.data.features;
            this.isLoading = false;
            console.log(apiSearchResult);
          } catch {
            this.isError = true;
            this.isLoading = false;
          }
          return
        }
      }, 250);
      } else {
        this.tooMutchCities = true
      }

    },
    getCity(searchResult) {
      const [city, state] = searchResult.place_name.split(",");
      const cityInfo = {
        id: searchResult.id,
        city: city,
        state: state,
        geo: {
          lat: searchResult.geometry.coordinates[1],
          lng: searchResult.geometry.coordinates[0],
        },
        preview: true,
      };
      this.cities.push(cityInfo);
      this.isLoading = false;
      this.apiSearchResult = [];
      this.searchInputValue = "";
    },
    removeCity(id) {
     this.cities = this.cities.filter((city) => city.id !== id)
    }
  }
};
</script>
<style scoped>
.input-search {
  border: none;
  outline: none;
  background-color: transparent;
  color: #e7f5dc;
  border-bottom: solid 1px #c0dfc2;
  width: 100%;
}
.input-search:focus {
  border-bottom: solid 1px #4b6154;
  box-shadow: 0px 18px 14px -3px rgba(75, 97, 84, 0.81);
}
.city-list {
  list-style: none;
  margin-top: 10px;
}
.city-list li {
  padding: 5px;
  margin-top: 5px;
}
.city {
  border: solid 1px #e7f5dc;
  padding: 5px;
  margin-top: 10px;
}
</style>
